<template>
  <div class="container">
    <h1 class="title">Main Page</h1>
    <div class="left-column">
      <RecipePreviewList
        title="Random Recipes"
        class="RandomRecipes center"
        ref="recipePreviewList"
        :get-recipes="getRandomRecipes"
      />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <button @click="loadRandomRecipes" class="load-button">
        Load More Recipes
      </button>
    </div>
    <div class="right-column">
      <template v-if="$root.store.username">
        <RecipePreviewList
          title="Last Viewed Recipes"
          :class="{
            RandomRecipes: true,
            blur: !$root.store.username,
            center: true
          }"
          :recipes="lastViewedRecipes"
        ></RecipePreviewList>
      </template>
      <template v-else>
        <router-link to="/login" tag="button" class="login-button">
          You need to Login to view this
        </router-link>
      </template>
    </div>
  </div>
</template>

<script>
import RecipePreviewList from "../components/RecipePreviewList";
export default {
  components: {
    RecipePreviewList
  },
  data() {
    return {
      lastViewedRecipes: []
    };
  },
  methods: {
    async loadRandomRecipes() {
      await this.$refs.recipePreviewList.updateRecipes();
    },
    async getRandomRecipes() {
      try {
        const response = await this.axios.get(
          this.$root.store.server_domain + "/recipes/random",
          { withCredentials: true }
        );
        return response.data;
      } catch (error) {
        console.log(error);
        return [];
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.left-column,
.right-column {
  flex-basis: 48%;
}

.RandomRecipes {
  margin: 10px 0 10px;
}

.load-button {
  display: block;
  margin-top: 10px;
}

.login-button {
  display: block;
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #42b983;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.blur {
  -webkit-filter: blur(5px);
  filter: blur(2px);
}

::v-deep .blur .recipe-preview {
  pointer-events: none;
  cursor: default;
}
</style>
